<template>
  <form class="form-register" @submit.prevent="register">
    <div class="logo-top-popup"></div>
    <h1 class="form-register__caption">{{ 'registration' | translate }}</h1>
    <!-- Social links -->
    <!--
    <SocialLinks action="register" />
    -->

    <!-- Email -->
    <div class="ir-form-group">
      <label for="email" class="ir-form-label">{{ 'email' | translate }}<span class="required">*</span></label>
      <input
        id="email"
        type="text"
        class="ir-input-field"
        :class="{ error: errors.email }"
        v-model="user.email"
        :placeholder="'email' | translate"
    />
      <p v-show="errors.email" class="ir-input-field-error">{{ errors.email | translate }}</p>
    </div>
    <!-- Phone number -->
    <div class="ir-form-group">
      <label for="phone" class="ir-form-label">{{ 'phone' | translate }}</label>
      <input
        id="phone"
        type="text"
        class="ir-input-field"
        :class="{ error: errors.phone }"
        v-model="user.phone"
        :placeholder="'phone' | translate"
      />
      <p v-show="errors.phone" class="ir-input-field-error">{{ errors.phone | translate }}</p>
    </div>
    <!-- Password -->
    <div class="ir-form-group">
      <label for="password" class="ir-form-label">{{ 'password' | translate }}<span class="required">*</span></label>
      <input
        id="password"
        :type="!passwordShow ? 'password' : 'text'"
        class="ir-input-field password"
        :class="{ error: errors.password }"
        v-model="user.password"
        :placeholder="'password' | translate"
      />
      <span :class="[ !passwordShow ? 'view-password-btn' : 'hide-password-btn' ]" @click="passwordShow = !passwordShow"></span>
      <p v-show="errors.password" class="ir-input-field-error">{{ errors.password | translate }}</p>
    </div>
    <!-- Confirm password -->
    <div class="ir-form-group">
      <label for="confirmPassword" class="ir-form-label">{{ 'confirmPassword' | translate }}<span class="required">*</span></label>
      <input
        id="confirmPassword"
        :type="!confirmPasswordShow ? 'password' : 'text'"
        class="ir-input-field password"
        :class="{ error: errors.confirmPassword }"
        v-model="user.confirmPassword"
        :placeholder="'confirmPassword' | translate"
      />
      <span :class="[ !confirmPasswordShow ? 'view-password-btn' : 'hide-password-btn' ]" @click="confirmPasswordShow = !confirmPasswordShow"></span>
      <p v-show="errors.confirmPassword" class="ir-input-field-error">{{ errors.confirmPassword | translate }}</p>
    </div>
    <!-- First name -->
    <div class="ir-form-group">
      <label for="firstName" class="ir-form-label">{{ 'firstName' | translate }}<span class="required">*</span></label>
      <input
        id="firstName"
        type="text"
        class="ir-input-field"
        :class="{ error: errors.firstName }"
        v-model="user.firstName"
        :placeholder="'firstName' | translate"
      />
      <p v-show="errors.firstName" class="ir-input-field-error">{{ errors.firstName | translate }}</p>
    </div>
    <!-- Last name -->
    <div class="ir-form-group">
      <label for="lastName" class="ir-form-label">{{ 'lastName' | translate }}<span class="required">*</span></label>
      <input
        id="lastName"
        type="text"
        class="ir-input-field"
        :class="{ error: errors.lastName }"
        v-model="user.lastName"
        :placeholder="'lastName' | translate"
      />
      <p v-show="errors.lastName" class="ir-input-field-error">{{ errors.lastName | translate }}</p>
    </div>
    <!-- reCaptcha (Временная заглушка!!!)-->
    <!-- <div class="form-register__re-captcha">reCaptcha</div> -->
    <!-- Submit button -->
    <div class="form-register__submit">
      <button
        type="submit"
        class="ir-blue-button"
        :class="{ 'disabled': !isActiveSubmitButton }"
      >
        {{ 'register' | translate }}
      </button>
      <span class="decorate-line"></span>
    </div>
    <!-- User agreement checkbox -->
    <input
      id="userAgreementFlag"
      class="form-register__user-agreement-flag"
      type="checkbox"
      v-model="userAgreementFlag"
    >
    <label for="userAgreementFlag" class="form-register__warning-text" v-html="$t('registerWarningText')"></label>
    <BottomLoginBox />
  </form>
</template>

<script>
// import SocialLinks from '@/components/SocialLinks.vue'
import BottomLoginBox from '@/components/BottomLoginBox.vue'

export default {
  name: 'FormRegister',
  components: {
    BottomLoginBox
//    SocialLinks
  },
  data () {
    return {
      user: {
        email: '',
        phone: '',
        password: '',
        confirmPassword: '',
        firstName: '',
        lastName: '',
      },
      errors: {
        email: '',
        phone: '',
        password: '',
        confirmPassword: '',
        firstName: '',
        lastName: '',
      },
      passwordShow: false,
      confirmPasswordShow: false,
      userAgreementFlag: false
    }
  },
  computed: {
    isActiveSubmitButton () {
      return this.user.email && this.user.password && this.user.confirmPassword && this.user.firstName && this.user.lastName && this.userAgreementFlag
    }
  },
  methods: {
    register () {
      if (!this.isActiveSubmitButton) return
      if (!this.validate()) return

      // Call register api here

      this.$router.push('confirm-register')
    },
    validate () {
      let result = true
      if (!this.validateEmail()) result = false
      if (!this.validatePhone()) result = false
      if (!this.validatePassword()) result = false
      if (!this.validateConfirmPassword()) result = false
      if (!this.validateFirstName()) result = false
      if (!this.validateLastName()) result = false

      return result
    },
    validateEmail () {
      this.errors.email = ''
      // Check required
      if (!this.user.email) {
        this.errors.email = 'mustbeEmail'
        return false
      }
      // Check RegExp
      const res = /^([a-z0-9_-]+\.)*[a-z0-9_-]+@[a-z0-9_-]+(\.[a-z0-9_-]+)*\.[a-z]{2,6}$/.test(this.user.email)
      if (!res) {
        this.errors.email = 'invalidEmail'
        return false
      }

      return true
    },
    validatePhone () {
      this.errors.phone = ''
      if (!this.user.phone) return true
      // Check RegExp
      // const res = /^\+\d{2}\(\d{3}\)\d{3}-\d{2}-\d{2}$/.test(this.user.phone)
      const res = /^\+[1-9]{1}[0-9]{7,11}$/.test(this.user.phone)
      // ^\+[1-9]{1}[0-9]{7,11}$ 
      if (!res) {
        this.errors.phone = 'invalidPhone'
        return false
      }

      return true
    },
    validatePassword () {
      this.errors.password = ''
      // Check required
      if (!this.user.password) {
        this.errors.password = 'mustbePassword'
        return false
      }

      // Check length
      if (this.user.password.length < 6 || this.user.password.length > 20) {
        this.errors.password = 'invalidPassword'
        return false
      }

      // Check RegExp
      const res = /^[a-zA-Z0-9\W]+$/.test(this.user.password)
      if (!res) {
        this.errors.password = 'invalidPassword'
        return false
      }

      return true
    },
    validateConfirmPassword () {
      this.errors.confirmPassword = ''
      if (this.user.password && this.user.password != this.user.confirmPassword) {
        this.errors.confirmPassword = 'invalidConfirmPassword'
        return false
      }

      return true
    },
    validateFirstName () {
      this.errors.firstName = ''
      // Check required
      if (!this.user.firstName) {
        this.errors.firstName = 'mustbeFirstName'
        return false
      }

      return true
    },
    validateLastName () {
      this.errors.lastName = ''
      // Check required
      if (!this.user.lastName) {
        this.errors.lastName = 'mustbeLastName'
        return false
      }

      return true
    }
  }
}
</script>

<style lang="scss">
.form-register {
  position: fixed;
  width: 416px;
  height: 780px;
  padding: 0 68px;
  background: url('../assets/img/ir/bg-register.png') 100% 100% no-repeat;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  &__caption {
    font-size: 36px;
    text-align: center;
    margin-top: 74px;
    margin-bottom: 20px;
    font-family: 'B52';
    font-size: 36px;
    font-weight: normal;
    letter-spacing: -1.35px;
    text-align: center;
    color: #5b4746;
  }
  &__re-captcha {
    /* Пока заглушка до получения домена */
    width: 155px;
    height: 40px;
    margin: 20px auto 16px auto;
    line-height: 40px;
    background-color: #777;
    text-align: center;
  }
  &__submit {
    display: block;
    position: relative;
    width: 190px;
    height: 40px;
    margin: 20px auto 10px auto;
  }
  &__user-agreement-flag {
    margin-right: 5px;
    position: relative;
    top: -2px;
  }
  &__warning-text {
    font-family: 'RobotoSlab';
    font-size: 12px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: 0.11px;
    text-align: center;
    color: #5b4746;
    & > a {
      color: #5b4746;
      &:hover {
        text-decoration: none;
      }
    }
  }
}
</style>
