<template>
  <form class="form-confirm-register" @submit.prevent="repeat">
    <div class="logo-top-popup"></div>
    <!-- Caption -->
    <h1 class="form-confirm-register__caption">{{ 'confirmRegistration' | translate }}</h1>
    <!-- Warning text -->
    <p class="form-confirm-register__warning-text">
      {{ 'confirmRegistrationWarningText' | translate }}
    </p>
    <!-- Submit button -->
    <div class="form-confirm-register__submit">
      <button
        type="submit"
        class="ir-blue-button"
        :class="{ 'disabled': !isActiveSubmitButton }"
      >
        {{ 'confirmRegistrationRepeat' | translate }} <span v-show="timeLeft > 0">({{ timeLeft / 1000 }} {{ 'confirmRegistrationSeconds' | translate }})</span>
      </button>
    </div>
    <BottomLoginBox />
  </form>
</template>

<script>
import BottomLoginBox from '@/components/BottomLoginBox.vue'

export default {
  name: 'FormConfirmRegister',
  components: {
    BottomLoginBox
  },
  data () {
    return {
      timeLeft: 30000,
      intervalId: null
    }
  },
  computed: {
    isActiveSubmitButton () {
      return this.timeLeft === 0
    }
  },
  methods: {
    repeat () {
      if (!this.isActiveSubmitButton) return
      alert('Send e-mail again')
      // Call 'send again' api here
      this.runTimer()
    },
    runTimer () {
      this.timeLeft = 30000
      this.intervalId = setInterval(() => {

        if (this.timeLeft >= 1000) {
          this.timeLeft -= 1000
        } else {
          this.timeLeft = 0
          clearInterval(this.intervalId)
          this.intervalId = null
        }
      }, 1000)
    }
  },
  created () {
    this.runTimer()
  },
  beforeDestroy () {
    if (this.intervalId !== null) clearInterval(this.intervalId)
  }
}
</script>

<style lang="scss">
.form-confirm-register {
  position: fixed;
  width: 416px;
  height: 397px;
  padding: 0 68px;
  background: url('../assets/img/ir/bg-confirm.png') 100% 100% no-repeat;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  .logo-top-popup {
    top: 12px;
  }
  &__caption {
    font-size: 36px;
    text-align: center;
    margin-top: 84px;
    margin-bottom: 20px;
    font-family: 'B52';
    font-size: 36px;
    font-weight: normal;
    line-height: 1;
    letter-spacing: -1.35px;
    text-align: center;
    color: #5b4746;
  }
  &__warning-text {
    font-family: 'RobotoSlab';
    font-size: 15px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.6;
    letter-spacing: 0.14px;
    text-align: center;
    color: #5b4746;
  }
  &__submit {
    display: block;
    position: relative;
    width: 222px;
    height: 40px;
    margin: 20px auto 10px auto;
  }
}
</style>
